import React from 'react';
import {RecordAdministrationComponent} from "./RecordAdministrationComponent";
import axios from 'axios';
axios.defaults.withCredentials = true;


export class RecordAdministrationContainer extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            personalId: '',
            duration: '',
            tlk: '',
            appDesc: '',
            vlk: '',
            repeated: '',
            date: '',
            patientId: '',
            doctorId: '',
            history: props.history
        }
    }

    componentDidMount = () => {
        axios.get('http://localhost:8081/api/userId/')
            .then((response) => {
                this.setState({doctorId: response.data});
                axios.get('http://localhost:8081/api/patients/' +  this.props.match.params.id)
                  .then((response) => {
                    const {id, personalId} = response.data;
                    this.setState({
                      personalId: personalId,
                      patientId: id
                    });

                  })
                  .catch((error) => {
                    console.log(error);
                  })
            })
            .catch((error) => {
                console.log(error);
            });
    };


    handleChange = (event) => {
        const target = event.target;
        const value = target.value;
        const id = target.id; // i input tag'o gaunam bsenos objekto rakt reikmei nustatyti
        this.setState({
                [id]: value
            }
        );
    };
    handleClick = (event) => {
        var today = new Date();
        var dd = today.getDate();
        var mm = today.getMonth() + 1; //January is 0!
        var yyyy = today.getFullYear();

        if (dd < 10) {
            dd = '0' + dd
        }

        if (mm < 10) {
            mm = '0' + mm
        }

        today = yyyy + '-' + mm + '-' + dd;

        const outputRecord = {
            personalId: this.state.personalId,
            duration: this.state.duration,
            tlk: this.state.tlk,
            appDesc: this.state.appDesc,
            vlk: this.state.vlk,
            repeated: this.state.repeated,
            date: this.state.date = today
        };
        if (this.state.personalId === "") {
            alert("Praome vesti asmens kod");
        }
        if (this.state.personalId.length>11) {
            alert("Asmens kodas turi bti sudarytas i 11 simboli");
        }
        if (this.state.personalId.length<11) {
            alert("Asmens kodas turi bti sudarytas i 11 simboli");
        }
        if (this.state.tlk === "") {
            alert("Praome pasirinkti TLK kod");
        }
        if (this.state.duration === "") {
            alert("Praome vesti vizito trukm");
        }
        if (this.state.appDesc === "") {
            alert("Praome vesti vizito apraym");
        }
        if (this.state.duration === "") {
            alert("Praome vesti vizito trukm");
        }
        if (this.state.repeated === "") {
            alert("Praome paymti ar vizitas pakartotinis");
        }
        if (this.state.date === "") {
            alert("Praome vesti vizito dat");
        }

        axios.post('http://localhost:8081/api/records/new/' + this.state.doctorId + '/' + this.state.patientId, outputRecord)
            .then((response) => {
                this.setState( {
                  personalId: '',
                  duration: '',
                  tlk: '',
                  appDesc: '',
                  vlk: '',
                  repeated: '',
                  date: ''
                });
                alert("Ligos istorija skmingai uregistruota");
                this.props.history.push("/doctor/records");
            })
            .catch((error) => {
                console.log(error);
            });
            event.preventDefault();
    };

    render() {
        return (
            <div>
                <RecordAdministrationComponent
                    personalId={this.state.personalId}
                    duration={this.state.duration}
                    tlk={this.state.tlk}
                    appDesc={this.state.appDesc}
                    vlk={this.state.vlk}
                    repeated={this.state.repeated}
                    date={this.state.date}
                    onChange={this.handleChange}
                    onClick={this.handleClick}
                    history={this.state.history}
                />
            </div>
        );
    }
}



