import React from 'react';


export var RecordAdministrationComponent = (props) => {

    return (
        <form className="form-horizontal">

            <div className="form-group">
                <label className="col-sm-2 control-label">Paciento asmens kodas</label>
                <div className="col-sm-3">
                    <input type="text" className="form-control" id="personalId" placeholder="Paciento asmens kodas" value={props.personalId}
                           onChange={props.onChange}/>
                </div>
            </div>

            <div className="form-group">
                <label className="col-sm-2 control-label">Vizito trukm</label>
                <div className="col-sm-3">
                    <select id="duration" value={props.duration}
                            onChange={props.onChange}>
                        <option></option>
                        <option>10 minui</option>
                        <option>20 minui</option>
                        <option>30 minui</option>
                        <option>40 minui</option>
                        <option>50 minui</option>
                        <option>60 minui</option>
                        <option>70 minui</option>
                        <option>80 minui</option>
                    </select>
                </div>
            </div>

            <div className="form-group">
                <label className="col-sm-2 control-label">TLK-10</label>
                <div className="col-sm-3">
                    <input type="text" className='form-control' id="tlk" placeholder="TLK-10" value={props.tlk}
                           onChange={props.onChange}/>
                </div>
            </div>


            <div className="form-group">
                <label className="col-sm-2 control-label">VLK</label>
                <div className="col-sm-3">
                    <select id="vlk" value={props.vlk}
                           onChange={props.onChange}>
                        <option></option>
                        <option>Taip</option>
                        <option>Ne</option>
                    </select>
                </div>
            </div>

            <div className="form-group">
                <label className="col-sm-2 control-label">Kartotinas</label>
                <div className="col-sm-3">
                    <select id="repeated" placeholder="Kartotinas" value={props.repeated}
                           onChange={props.onChange}>
                        <option></option>
                        <option>Kartotinas</option>
                        <option>Nekartotinas</option>
                    </select>
                </div>
            </div>
            <div className="form-group">
                <label className="col-sm-2 control-label">Vizito apraymas</label>
                <div className="col-sm-3">
                    <textarea type="text" className="form-control" id="appDesc" placeholder="Vizito apraymas" value={props.appDesc}
                           onChange={props.onChange}/>
                </div>
            </div>

            <div className="form-group">
                <div className="col-sm-offset-2 col-sm-3">
                    <button type="submit" className="btn btn-success" onClick={props.onClick}>Save</button>
                    <button type="submit" className="btn btn-default" onClick={props.history.goBack}>Cancel</button>
                </div>
            </div>
        </form>
    );
};



