import React, {Component} from 'react';
import {API} from '../ApiUrl';
import RegisterAdminComponent from './RegisterAdminComponent';
import axios from 'axios';

axios.defaults.withCredentials = true;

class RegisterAdmin extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            surname: '',
            username: '',
            password: '',
            history: props.history
        }
    }

    handleChange = (event) => {
        const target = event.target;
        const value = target.value;
        const id = target.id; // i input tag'o gaunam bsenos objekto rakt reikmei nustatyti
        this.setState({
                [id]: value
            }
        );
    };

    EmptyFields() {
        if (this.state.name === "" || this.state.surname === "" || this.state.username === "" || this.state.personalId === "") {
            alert("Visi laukai turi bti upildyti");
        } else return true;
    }

    handleClick = (event) => {
        if (this.EmptyFields()) {
            var outputAdmin = {
                name: this.state.name,
                surname: this.state.surname,
                username: this.state.username,
                password: this.state.password
            };
        }
        if (this.state.password.length < 6) {
            alert("Slaptaodis turi bti sudarytas i 6 arba daugiau simboli");
        }
        axios.post(API + "/api/admin/admins/new", outputAdmin)
            .then((response) => {
                this.setState({
                    name: '',
                    surname: '',
                    username: '',
                    password: ''
                });
                alert("Administratorius uregistruotas!");
                this.props.history.push("/admin/admins");
            })
            .catch((error) => {
                console.log(error);
            });
        event.preventDefault();
    };

    render() {
        return (
            <div>
                <RegisterAdminComponent
                    name={this.state.name}
                    surname={this.state.surname}
                    username={this.state.username}
                    password={this.state.password}
                    onChange={this.handleChange}
                    onClick={this.handleClick}
                    history={this.state.history}
                />
            </div>
        );
    }
}

export default RegisterAdmin;



