import React from 'react';

const RegisterPatientComponent = (props) => {
  console.log(props);
  return(
    <form className="form-horizontal">

              <div className="form-group">
                  <label className="col-sm-2 control-label">Vardas</label>
                  <div className="col-sm-3">
                      <input type="text" className="form-control" id="name" placeholder="Vardas" value={props.name}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <label className="col-sm-2 control-label">Pavard</label>
                  <div className="col-sm-3">
                      <input type="text" className="form-control" id="surname" placeholder="Pavard" value={props.surname}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <label className="col-sm-2 control-label">Slapyvardis</label>
                  <div className="col-sm-3">
                      <input type="text" className="form-control" id="username" placeholder="Slapyvardis" value={props.username}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <label className="col-sm-2 control-label">Slaptaodis</label>
                  <div className="col-sm-3">
                      <input type="text" className="form-control" id="password" placeholder="Slaptaodis" value={props.password}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <label className="col-sm-2 control-label">Asmens kodas</label>
                  <div className="col-sm-3">
                      <input type="number" className="form-control" id="personalId" placeholder="Asmens kodas" value={props.personalId}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <label className="col-sm-2 control-label">Gimimo data</label>
                  <div className="col-sm-3">
                      <input type="date" className="form-control" id="dateOfBirth" placeholder="Gimimo data" value={props.dateOfBirth}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <div className="col-sm-offset-2 col-sm-3">
                      <button type="submit" className="btn btn-success" onClick={props.onClick}>Isaugoti</button>
                      <button type="submit" className="btn btn-default" onClick={props.history.goBack}>Ataukti</button>
                  </div>
              </div>
          </form>
  );
};

export default RegisterPatientComponent;



