import React from 'react';

const EditPrescriptionComponent = (props) => {
  return(
    <form className="form-horizontal">

        <div className="form-group">
            <label className="col-sm-2 control-label">Veiklioji mediaga</label>
            <div className="col-sm-3">
                <input type="text" className="form-control" id="activeMat" placeholder="Veiklioji mediaga" value={props.activeMat}
                       onChange={props.onChange}/>
            </div>
        </div>

        <div className="form-group">
            <label className="col-sm-2 control-label">Veikliosios mediagos kiekis vienoje dozje</label>
            <div className="col-sm-3">
                <input type="text" className="form-control" id="activeMatQuantity" placeholder="Veikliosios mediagos kiekis" value={props.activeMatQuantity}
                       onChange={props.onChange}/>
            </div>
        </div>

        <div className="form-group">
            <label className="col-sm-2 control-label">Veikliosios mediagos matavimo vienetai</label>
            <div className="col-sm-3">
                <select id="unit" value={props.unit}
                       onChange={props.onChange}>
                    <option></option>
                    <option>Miligramai</option>
                    <option>Mikrogramai</option>
                    <option>TV/IU</option>
                </select>
            </div>
        </div>

        <div className="form-group">
            <label className="col-sm-2 control-label">Vartojimo apraymas</label>
            <div className="col-sm-3">
                <input type="text" className="form-control" id="desc" placeholder="Apraymas" value={props.desc}
                       onChange={props.onChange}/>
            </div>
        </div>

        <div className="form-group">
            <label className="col-sm-2 control-label">Galiojimo data</label>
            <div className="col-sm-3">
                <input type="date" className="form-control" id="validUntil" placeholder="Galiojimo data" value={props.validUntil}
                       onChange={props.onChange}/>
                <select id="validUntil" value={props.validUntil} onChange={props.onChange}>
                    <option></option>
                    <option>Neterminuotas</option>
                </select>
            </div>
        </div>
        <div className="form-group">
            <div className="col-sm-offset-2 col-sm-3">
                <button type="submit" className="btn btn-success" onClick={props.onClick}>Isaugoti</button>
                <button type="submit" className="btn btn-default" onClick={props.history.goBack}>Ataukti</button>
            </div>
        </div>
    </form>
  );
};

export default EditPrescriptionComponent;



