import React from 'react';

const EditDoctorComponent = (props) => {
  return(
    <form className="form-horizontal">

              <div className="form-group">
                  <label className="col-sm-2 control-label">Vardas</label>
                  <div className="col-sm-3">
                      <input type="text" className="form-control" id="name" placeholder="Vardas" value={props.name}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <label className="col-sm-2 control-label">Surname</label>
                  <div className="col-sm-3">
                      <input type="text" className="form-control" id="surname" placeholder="Pavard" value={props.surname}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <label className="col-sm-2 control-label">Vartotojo vardas</label>
                  <div className="col-sm-3">
                      <input type="text" className="form-control" id="username" placeholder="Vartotojo vardas" value={props.username}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <label className="col-sm-2 control-label">Specializacija</label>
                  <div className="col-sm-3">
                      <select id="specialisation" placeholder="Specializacija" value={props.specialisation}
                             onChange={props.onChange}>
                          <option></option>
                          <option>Apylinks pediatras</option>
                          <option>Akueris-ginekologas</option>
                          <option>Chirurgas</option>
                          <option>Psichiatras</option>
                          <option>Vaik ir paaugli psichiatras</option>
                          <option>Priklausomybs lig psichiatras</option>
                          <option>Medicinos psichologas</option>
                          <option>Medicinos gydytojas</option>
                          <option>Vidaus lig gydytojas</option>
                          <option>Geriatras</option>
                          <option>Kardiologas</option>
                          <option>Vaik kardiologas</option>
                          <option>Reumatologas</option>
                          <option>Vaik reumatologas</option>
                          <option>Gastroenterologas</option>
                          <option>Vaik gastroenterologas</option>
                          <option>Nefrologas</option>
                          <option>Vaik nefrologas</option>
                          <option>Endokrinologas</option>
                          <option>Vaik endokrinologas</option>
                          <option>Hematologas</option>
                          <option>Vaik hematologas</option>
                          <option>Alergologas</option>
                          <option>Vaik alergologas</option>
                          <option>Pulmonologas</option>
                          <option>Vaik pulmonologas</option>
                          <option>Infektologas</option>
                          <option>Toksikologas    </option>
                          <option>Ftiziatras</option>
                          <option>Vaik ftiziatras</option>
                          <option>Dermatovenerologas</option>
                          <option>Neurologas</option>
                          <option>Vaik neurologas</option>
                          <option>Vaik lig gydytojas</option>
                          <option>Neonatologas</option>
                          <option>Anesteziologas-reanimatologas</option>
                          <option>Vaik anesteziologas</option>
                          <option>Chirurgas</option>
                          <option>Vaik chirurgas</option>
                          <option>Traumatologas-ortopedas</option>
                          <option>Vaik ortopedo traumatologas</option>
                          <option>Vaik urologas</option>
                          <option>Vaik urologas</option>
                          <option>Neurochirurgas</option>
                          <option>Vaik neurochirurgas</option>
                          <option>Kraujagysli chirurgas</option>
                          <option>Koloproktologo</option>
                          <option>Krtins chirurgas</option>
                          <option>irdies chirurgas</option>
                          <option>Plastins ir rekonstrukcins chirurgijos gydytojo</option>
                          <option>Veido ir andikauli chirurgo</option>
                          <option>Akueris-ginekologas</option>
                          <option>Oftalmologas</option>
                          <option>Vaik oftalmologas</option>
                          <option>Otorinolaringologas</option>
                          <option>Onkologas</option>
                          <option>Mamologas</option>
                          <option>Onkologas radioterapeutas</option>
                          <option>Onkologo chemioterapeutas</option>
                          <option>Psichiatras</option>
                          <option>Psichoterapeutas</option>
                          <option>Vaik ir paaugli psichiatras</option>
                          <option>Darbo medicinos gydytojas</option>
                          <option>Genetikas</option>
                          <option>Odontologas</option>
                          <option>Burnos chirrugas</option>
                          <option>Ortodontas</option>
                          <option>Ortopedas odontologas</option>
                          <option>Endodontologas</option>
                          <option>Periodontologas</option>
                          <option>Vaik odontologas</option>
                          <option>Burnos, veido ir andikauli chirurgas</option>
                          <option>Dietologas</option>
                          <option>Sporto medicinos gydytojas</option>
                      </select>
                      <input type="text" className="form-control" id="specialisation" placeholder="Kita specializacija" value={props.specialisation}
                             onChange={props.onChange}/>
                  </div>
              </div>

              <div className="form-group">
                  <div className="col-sm-offset-2 col-sm-3">
                      <button type="submit" className="btn btn-success" onClick={props.onClick}>Isaugoti</button>
                      <button type="submit" className="btn btn-default" onClick={props.history.goBack}>Ataukti</button>
                  </div>
              </div>
          </form>
  );
};

export default EditDoctorComponent;



